a{
    text-decoration: none;
    background-color: rgb(88, 100, 110);
    color:var(--light);
    transition: 0.2s ease-in-out;

  
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.582);
    padding: 0.2em;
}
.projectsDiv{
    background-color: var(--light);
    margin: 0 auto;
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.582);


}

.project-desc{
    font-size: calc(0.6rem + 0.7vw);
    padding-left : 3em;
    padding-right : 3em;
    padding-top : 1em;
    line-height: 2em;
    letter-spacing:0.1q;
    text-align: center;
}

.projImg{
    width: 80%;
    box-shadow: 0px 0px 9px rgba(0, 0, 0, 0.582);
    object-fit: scale-down;
}

.card{
    background-color:var(--accent);
    display: flex;
    flex-direction: column;
    margin-top: 1em;
    box-shadow: 0px 0px 0.3em rgba(34, 29, 29, 0.842);
    border-radius: 0.4em;
    border: calc(1px + 0.2vw) solid rgb(25, 52, 63);
    align-items: center;
    height: fit-content;
    padding: 0.5em;
    margin: 2em;
 
}

.card>h3,h4,h5,h6{
    color:rgb(15, 1, 1);
    letter-spacing:0.4rem;
    font-size:1.6rem;
    text-shadow: 0px 0px 0.1em rgba(104, 99, 99, 0.493);
}
.cardContainer{
    display: flex;
    flex-direction: column;
}
.containerbtns{
    width: 80%;
    padding-bottom: 1em;
    justify-content: center;
    gap:calc(1em + 0.5vw);
    margin-bottom: 4em ;
    margin-top: auto;
}

a:hover{
    background-color: rgb(88, 121, 143);
    transform: scale(1.1);
    transition: 0.2s ease-in-out;
}

/* Computer Styles */
@media (min-width: 540px){
    .projectsDiv{
        width: 95%;
        height: auto;
        padding-left: 04em;
    
        grid-area: projects;
    }

    .card{
        font-size: calc(100% + 1.05vw);
        flex-basis: auto;
    margin: 2em;
       
        width: 90%;
        height: 60vh;
    }
.project-desc{
    line-height: 1.2em;

}
    .containerbtns{
        width:90%;
      margin: auto;
    }

    .containerbtns>a{
        width: 35%;
        
        font-size:calc(30% + 1.05vw);
    }
    .cardContainer{
        flex-direction: row;
        gap: 4em;
        flex-wrap: nowrap;
        justify-content: space-evenly;
    }
}