.list-element{
    width:50%;
    padding:calc(0.2rem + 0.5vw);
    padding-bottom: 1em;
    margin: auto;
}
.skillsDiv{
    background-color: var(--light);
    margin-top:0;
    box-shadow: 0px 0px 0.9em rgba(0, 0, 0, 0.582);

}

.skillicon{
    background-color: #00000000;
    width: 16px;
    height: 16px;
    padding:2px;
    object-fit: cover;
    transition: 0.2s ease-in-out;
}
.skillicon:hover{
    transform: scale(2);
    transition: 0.3s ease-in-out;
}
@media (max-width:540px) {
    .skillsDiv{
       
    }
}
@media (min-width: 540px){
    .skillsDiv{
        color:var(--dark);
        margin:5em;
        padding:1em;
        width:90%;
        grid-area: skills;

        transform: translateX(-8em)
    }

    .skillicon{
      
    }
}